body {
  text-align: center;
  font-family: Arial, sans-serif;
}

.search-form {
  height: 60px;
  background-color: #3434c2;
  border-radius: 20px;
  margin-bottom: 20px;
}

.search-form input {
  height: 20px;
  border: #3434c2;
  border-radius: 7px;
  margin-top: 17px;
}

.search-form button {
  color: gray;
  height: 23px;
  cursor: pointer;
  background-color: #fff;
  border: #3434c2;
  border-radius: 5px;
  margin-left: 10px;
}

.gallery {
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  margin-bottom: 20px;
  display: grid;
}

.photo-card {
  border: 1px solid #ccc;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.info {
  background-color: #f9f9f9;
  padding: 10px;
}

.info-item {
  margin-bottom: 5px;
  font-size: 14px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
}

.load-more.hidden {
  display: none;
}

